.table{
	font-family: 'Font-answers', sans-serif;
	margin-left: auto;
	margin-right: auto;
}

.table td {
	width: 5.5vw;
	height: 5.5vw;
	text-align: center;
	border: none;
	touch-action: none;
	user-select: none;
}

/*@media (min-width: 800px) {
	.table td {
		width: 2rem;
		height: 2rem;
	}
}*/

.table,.table td,.table th,.table tr{
	border-radius: 100%;
}

.info{
	font-family: 'Font-answers', sans-serif;
	font-size: 16px;
	color: #002D76;
}

.margin{
	text-align: center;
}

.wordContainer{
	max-width: 700px;
	display: grid;
	grid-template-columns: auto auto auto auto;
	font-family: 'Font-answers', sans-serif;
	color: #002D76;
	padding-bottom: 3em;
	margin-left: auto;
	margin-right: auto;
}

.wordContainer p{
	margin: 0 0;
}

.clockContainer{
	background-color: #4B74F4;
	width: 5em;
	height: 2em;
	margin-left: auto;
	margin-right: auto;
	display:flex;
	justify-content:center;
	align-items:center;
	border-radius: 30px;
	color: white;
	margin-bottom: 1em;
}

.button{
	composes: button from './button.module.css';
	color: white;
	background-color: #4B74F4;
	border: none;
	font-size: 20px;
	text-align: center;
	display: block;
	margin: 1rem auto 0;
}

.score{
	font-size: 60px;
	color: #002D76;
	background-image: url("./../assets/SCORE CIRCLE.svg");
	margin-left: auto;
	margin-right: auto;
	margin-top: 1em;
	text-align: center;
	height: 5em;
	background-repeat: no-repeat;
	background-position: center;
	display: grid;
	grid-template-rows: auto auto;

}

.scoreText{
	font-size: 40px;
	align-self: center;

}

.back{
	composes: button;
	margin-top: 4em;
}

.retry{
	composes: button;
	color: white;
	margin-top: 1em;
	background-color: #002D76;
}
