.selected{
	color: white;
	background-color: #4B74F4;
	border: none;
	font-family: 'Font-answers', sans-serif;
	margin-top: 1em;
	width: 16em;
	font-size: 24px;
	height: 4em;
	place-self: center;
}

.button{
	composes: button from './button.module.css';
	font-family: 'Font-answers', sans-serif;
	margin-top: 1em;
	width: 16em;
	font-size: 24px;
	place-self: center;
}

.buttons{
	display: grid;
	grid-template-rows: auto auto auto auto auto;
}

.next{
	composes: button from './button.module.css';
	font-family: 'Font-answers', sans-serif;
	margin-top: 1em;
	width: 16em;
	font-size: 24px;
	background-color: #4B74F4;
	color: white;
	border: none;
	place-self: center;
}

.question{
	color: #002D76;
	font-size: 30px;
	text-align: center;
	margin: 0 1rem;
}

.score{
	font-size: 60px;
	color: #002D76;
	background-image: url("./../assets/SCORE CIRCLE.svg");
	margin-left: auto;
	margin-right: auto;
	margin-top: 1em;
	text-align: center;
	height: 5em;
	background-repeat: no-repeat;
	background-position: center;
	display: grid;
	grid-template-rows: auto auto;

}

.scoreText{
	font-size: 40px;
	align-self: center;

}

.back{
	composes: button;
	background-color: #4B74F4;
	border: none;
	color: white;
	display: block;
	margin: 4rem auto 0;
}

.retry{
	composes: button;
	background-color: #002D76;
	color: white;
	display: block;
	margin: 1rem auto 0;
}
